* { margin: 0; padding: 0; box-sizing: border-box; }

body { 
    font: 13px Helvetica, Arial; 
}

form input {
    display: inline-block;
}

input:focus {
    outline: none;
}

#emojiBox {
    position: fixed;
    right: 0;
    bottom: 40px;
}

.message { 
    background: rgb(230,230,230); 
    padding: 3px; 
    position: fixed; 
    bottom: 0; 
    width: 100%;
    display: flex;
    align-items: center;
}

.message input { 
    border: 0; 
    padding: 10px; 
    width: 87%; 
    margin-right: 0; 
    border-radius: 30px;
}

.message a {
    display: inline-flex;
    width: 3%;
    height: 33px;
    justify-content: center;
    align-items: center;
    margin-right: 0.5%;
    margin-left: 0;
}

#emoji {
    margin: auto;
}

.message button { 
    width: 9%; 
    background: rgb(130, 224, 255); 
    border: none; 
    padding: 10px; 
}

#rightBar {
    display: inline-flex;
    flex-direction: column;
    position: fixed;
    width: 9%;
    float: right;
}

#users {
    list-style-type: none;
}

i:hover {
    opacity: 0.6;
}

#messages {
    display: inline-block;
    overflow: auto;
    width: 91%;
    list-style-type: none; 
    margin: 0; 
    margin-bottom: 3%;
    padding: 0; 
}

#messages li {
    width: 99%;
    padding: 5px 10px; 
    display: inline-block;
}

#messages li:nth-child(odd) { 
    background: #eee; 
}

#me {
    color: salmon;
}
